html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  background-image: url("../images/theme/background-stripes_short.jpg");
  background-repeat: repeat-x;
  background-position: top center; }

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
  display: block;
  font-family: "Ubuntu", "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-weight: 700;
  font-style: italic;
  margin: 0 0 1.5rem 0; }

h1 a, .h1 a, h2 a, .h2 a, h3 a, .h3 a, h4 a, .h4 a, h5 a, .h5 a {
  text-decoration: none; }

h1, .h1 {
  font-size: 2.6875em;
  line-height: 1.2; }

h2, .h2 {
  font-size: 2.125em;
  line-height: 1.3; }

h3, .h3 {
  font-size: 1.625em; }

h4, .h4 {
  font-size: 1.25em;
  font-weight: 700; }

h5, .h5 {
  font-size: 1em;
  line-height: 2.09;
  text-transform: uppercase;
  letter-spacing: 2px; }

a {
  transition: all 0.2s; }
  a:hover {
    transition: all 0.2s; }

figure {
  margin: 0; }

img {
  margin: 0 0 1.5rem 0;
  border-radius: 3px; }

@media only screen and (min-width: 40.0625em) {
  img {
    margin: 0 0 1.5rem 0; }
    img.float-left,
    img.alignleft {
      margin-right: 1.5rem; }
    img.float-right,
    img.alignright {
      margin-left: 1.5rem; } }

.radius {
  border-radius: 3px; }

hr {
  display: block;
  width: 100%;
  height: 3px;
  margin: 3rem 0;
  padding: 0;
  border: 0 none;
  outline: 0 none;
  background: url("../images/theme/dotted-line-horizontal.png") top center repeat-x; }
  hr:before, hr:after {
    content: " ";
    display: table; }
  hr:after {
    clear: both; }

.image-border {
  display: block;
  padding: 5px !important;
  background: #f4f4f4;
  transition: all 0.15s;
  border-radius: 5px; }
  .image-border img {
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    text-decoration: none;
    border-radius: 2px; }
  .image-border:hover {
    background: #dcdcdc; }

.sub-titulek {
  display: block;
  font-size: 130%;
  font-style: italic;
  color: #781036; }

.clearfix:before, .clearfix:after {
  content: " ";
  display: table; }

.clearfix:after {
  clear: both; }

@media only screen and (max-width: 40em) {
